import React, { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import { Link, navigate } from 'gatsby';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import GlobalStyles from '@mui/material/GlobalStyles';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import MuiLink from '@mui/material/Link';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableRow from '@mui/material/TableRow';
import Typography from '@mui/material/Typography';
import Logo from '../../images/logo-fff.svg';
import Chara01 from '../../images/character-01-180x492.png';

import partnersData from '../../datas/partners';

const fontFamily = [
  'Noto Sans JP',
  'Helvetica Neue',
  'Arial',
  'Hiragino Kaku Gothic ProN',
  'Hiragino Sans',
  'Meiryo',
  'sans-serif'
]
  .map(f => `"${f}"`)
  .join(',');

const orange = '#ef8468';

const theme = createTheme({
  palette: {
    primary: {
      main: orange
    }
  },
  typography: {
    fontFamily,
    h3: {
      color: '#403939',
      fontFamily,
      fontSize: 24,
      lineHeight: '36px',
      fontWeight: 'bold'
    },
    body1: {
      fontFamily,
      fontSize: 16,
      lineHeight: '30px'
    }
  }
});

theme.typography.h2 = {
  color: orange,
  fontFamily,
  fontSize: 28,
  lineHeight: '42px',
  fontWeight: 'bold',
  [theme.breakpoints.up('sm')]: {
    fontSize: 32
  }
};

const CustomTable = ({ datas }) => (
  <TableContainer>
    <Table>
      <TableBody>
        {datas.map(({ key, value, link }, i) => (
          <TableRow key={i}>
            <TableCell
              sx={{
                p: 0
              }}
            >
              <Typography fontWeight='bold'>{key}</Typography>
            </TableCell>
            <TableCell>
              {link ? (
                <MuiLink href={link} color='primary'>
                  <Typography color='primary'>{value}</Typography>
                </MuiLink>
              ) : (
                <Typography>{value}</Typography>
              )}
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  </TableContainer>
);

const PartnersIndex = () => {
  const [uuid, setUuid] = useState('');
  const [partnerData, setPartnerData] = useState('init');

  useEffect(() => {
    const params = new URLSearchParams(window.location.search);
    const uuid = params.get('uuid');
    setUuid(uuid);
  }, []);

  useEffect(() => {
    if (uuid === '') return;
    setPartnerData(partnersData(uuid));
  }, [uuid]);

  if (partnerData === 'init') {
    return <p>Loading...</p>;
  } else if (partnerData === undefined) {
    navigate('/');
    return <></>;
  }

  return (
    <ThemeProvider theme={theme}>
      <GlobalStyles
        styles={{
          '*, html, body': { margin: 0, padding: 0 }
        }}
      />
      <Helmet>
        <html lang='ja' />
        <meta http-equiv='content-language' content='ja' />
        <meta name='robots' content='noindex' />
        <meta name='googlebot' content='notranslate' />
        <meta name='google' content='notranslate' />
        <title>PARTNERS | DARUMA, Inc.</title>
      </Helmet>
      <Container
        maxWidth={false}
        sx={{
          bgcolor: '#3c3c3c',
          px: {
            xs: 0,
            sm: 3,
            md: 4
          }
        }}
      >
        <Link to='/'>
          <Box
            pt={5}
            sx={{
              m: {
                xs: '0 auto 40px',
                sm: '0 0 0 40px'
              },
              width: {
                xs: 54,
                sm: 108
              },
              height: {
                xs: 28,
                sm: 56
              },
              backgroundImage: `url(${Logo})`,
              backgroundPosition: 'center center',
              backgroundRepeat: 'no-repeat',
              backgroundSize: 'contain'
            }}
          />
        </Link>
        <Box ml={2}>
          <Typography color='#f0f0f0' fontSize={24}>
            担当者紹介
          </Typography>
          <Typography
            variant='h1'
            color='#f0f0f0'
            fontWeight='bold'
            mb={{
              xs: '-12px',
              md: '-28px'
            }}
            fontSize={{
              xs: 50,
              md: 106
            }}
          >
            PARTNER
          </Typography>
        </Box>
        <Box position='relative' bgcolor='#f0f0f0' py={12} borderRadius={4}>
          <Box
            position='absolute'
            sx={{
              top: {
                xs: '-75px',
                sm: '-150px'
              },
              right: {
                xs: 10,
                sm: 30,
                md: 200
              },
              width: {
                xs: 45,
                sm: 90
              },
              height: {
                xs: 123,
                sm: 246
              },
              backgroundImage: `url(${Chara01})`,
              backgroundSize: 'contain',
              backgroundRepeat: 'no-repeat',
              backgroundPosition: 'center center'
            }}
          />
          <Container maxWidth='md'>
            <Box
              sx={{
                margin: '0 auto',
                width: {
                  xs: '100%',
                  sm: '500px'
                }
              }}
            >
              <Typography
                variant='h2'
                align='center'
                mb={4}
                sx={{
                  fontSize: {
                    xs: 18,
                    sm: 32
                  },
                  lineHeight: 2
                }}
              >
                私たちプロフェッショナルに、
                <br />
                なんでもご相談ください。
              </Typography>
              <Typography>
                ダルマでは一人一人に、不動産投資のプロフェッショナルがつきます。わからないこと、聞きたいことがあれば気軽にご相談ください。担当者が、お客さまに合わせた最適な提案をさせていただきます。
              </Typography>
            </Box>
            <Grid container spacing={2} mt={8}>
              <Grid item xs={12}>
                <Box
                  sx={{
                    padding: {
                      xs: '20px',
                      sm: '40px'
                    },
                    backgroundColor: '#fff',
                    borderRadius: '8px'
                  }}
                >
                  <Grid container>
                    <Grid item xs={12} sm={12} md={6}>
                      <Box
                        sx={{
                          margin: '0 auto',
                          width: {
                            xs: '100%',
                            sm: '335px'
                          },
                          height: {
                            xs: 'auto',
                            sm: '280px'
                          }
                        }}
                      >
                        <img src={partnerData.image} alt='Icon' width='100%' />
                      </Box>
                    </Grid>
                    <Grid item xs={12} sm={12} md={6}>
                      <Typography color='#3b4043'>
                        {partnerData.role}
                      </Typography>
                      <Typography variant='h2'>{partnerData.name}</Typography>
                      <Typography
                        color='#3b4043'
                        sx={{
                          mt: '-6px !important'
                        }}
                      >
                        {partnerData.name_en}
                      </Typography>
                      <CustomTable datas={partnerData.contacts} />
                    </Grid>
                  </Grid>
                </Box>
              </Grid>
            </Grid>
            <Grid container spacing={2} mt={0}>
              <Grid item xs={12} md={6} display='flex' alignSelf='stretch'>
                <Box
                  width='100%'
                  sx={{
                    padding: {
                      xs: '20px',
                      sm: '40px'
                    },
                    backgroundColor: '#fff',
                    borderRadius: '8px'
                  }}
                >
                  <Typography variant='h2' mb={3}>
                    担当者から一言
                  </Typography>
                  {partnerData.copy && (
                    <Typography variant='h3' mb={3}>
                      {partnerData.copy}
                    </Typography>
                  )}
                  <Typography>{partnerData.message}</Typography>
                </Box>
              </Grid>
              <Grid item xs={12} md={6} display='flex' alignSelf='stretch'>
                <Box
                  width='100%'
                  sx={{
                    padding: {
                      xs: '20px',
                      sm: '40px'
                    },
                    backgroundColor: '#fff',
                    borderRadius: '8px'
                  }}
                >
                  <Typography variant='h2' mb={3}>
                    基本情報
                  </Typography>
                  <CustomTable datas={partnerData.infos} />
                </Box>
              </Grid>
            </Grid>
          </Container>
        </Box>
        <Box
          component='footer'
          display='flex'
          alignItems='center'
          justifyContent='center'
          height={80}
        >
          <Typography color='#fff' fontSize={12}>
            &copy; 2022 DARUMA, Inc.
          </Typography>
        </Box>
      </Container>
    </ThemeProvider>
  );
};

export default PartnersIndex;
